.header {
    display : flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    width: 100%;
}

.header__icon {
    object-fit: contain;
    height: 80px;
    margin-left: 8%;
}

.header__center {
    display: flex;
    align-items: center;
    flex: 1;
    max-width: fit-content;
    padding : 10px;
    height: 30px;
    border: solid 1px lightgray;
    border-radius: 999px;
}

.header__center > input {
    border : none;
    padding : 10px;
    outline-width: 0;
}

.header__right {
    display: flex;
    align-items: center;
    margin-right: 80px;
    justify-content: space-between;
}

